export default {
  name: 'EmTag',
  props: {
    type: {
      // 标签类型 default、primary、success、warning、danger、royal, dark
      type: String,
      default: 'default',
    },
    size: {
      // 标签大小 lg, md, xs
      type: String,
      default: 'md',
    },
    // 标签内容
    text: {
      type: String,
      default: '',
    },
    disabled: {
      // 是否为禁用状态
      type: [String, Boolean],
      default: false,
    },
    inverted: {
      // 是否为空心
      type: [String, Boolean],
      default: false,
    },
    circle: {
      // 是否为圆角样式
      type: [String, Boolean],
      default: false,
    },
    mark: {
      // 是否为标记样式 left, right
      type: String,
      default: '',
    },
  },
  methods: {
    onClick() {
      if (this.disabled === true || this.disabled === 'true') {
        return;
      }
      this.$emit('click');
    },
  },
};
